.main {
  background-image: url("img/background.jpg");
  display: flex;
  justify-content: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  position: relative;
  margin: auto;
}

.main-content {  /* ЭКРАН МОНИТОРА НОУТА */
  flex-shrink: 0;
  margin: auto;
  padding: 51px 363px 155px 363px;
  margin-top: 347px;
}

.title {
  font-family: 'Roboto Bold', sans-serif;
  font-weight: 700;
  font-size: 35px;
  color: #FEFEFEFF;
  text-align: center;
  margin-top: 80px;
}

.title-description {
  font-family: 'Roboto Light', sans-serif;
  font-size: 14px;
  color: #B0A88A;
  text-align: center;
  padding: 10px;
}

.boxes-input {
  display: flex;
  width: 264px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px;
  margin: auto;
  padding-top: 25%;
}

.box-input-login {
  display: flex;
  align-items: center;
  width: 218px;
  height: 35px;
  border-radius: 6px;
  border: 1px solid #7E6553;
  background: rgba(79, 79, 79, 0.5);
  padding: 5px 15px;
}

.box-input-login:hover {
  background: rgba(103, 103, 103, 0.5);
  transition: 1s;
}

.input-login {
  background: none;
  border: none;
  box-shadow: none;
  outline: none;
  padding: 10px 0 10px 20px;
  font-family: 'Roboto Medium', sans-serif;
  caret-color: #B0A88A;
  color: wheat;
}

.input-login::placeholder {
  color: #B0A88A;
  font-family: 'Roboto Medium', sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.input-image-login {
  background-image: url("img/image-login.svg");
  background-size: cover;
  background-position: center;
  width: 15px;
  height: 17px;
}

.input-image-password {
  background-image: url("img/image-password.svg");
  background-size: cover;
  background-position: center;
  width: 15px;
  height: 17px;
}

.input-image-password-repeat{
  background-image: url("img/image-password-repeat.svg");
  background-size: cover;
  background-position: center;
  width: 15px;
  height: 17px;
}

.input-image-email {
  background-image: url("img/image-email.svg");
  background-size: cover;
  background-position: center;
  width: 15px;
  height: 17px;
}

.reg-button {
  border-radius: 6px;
  background: #BB9761;
  display: flex;
  padding: 10px 100px;
  margin: auto;
  font-family: 'Roboto Bold', sans-serif;
  font-size: 12px;
  color: #49372A;
  border: none;
  transition: all 200ms linear;
}

.reg-button:hover {
  filter: drop-shadow(0 0 1vh rgba(252,103,103,0.4));
  transition: all 200ms linear;
}

.change-page-ref
{
  font-weight: 500;
  transition: all 100ms linear;
  color: #e5dbb6;
}

.change-page-ref:hover
{
  transition: all 100ms linear;
  color: #e1aa49;
  cursor: pointer;
}